@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body{font-family:'Roboto', sans-serif;box-sizing:border-box;background-color:#E7ECF2;font-size:16px}*::after,*::before{box-sizing:border-box}img{object-fit:cover;display:block;max-width:100%;height:auto}p{margin-bottom:0}

.profile{margin-left:50px;margin-top:50px;box-shadow:10px 10px 6px -6px #777;width:300px;background-color:#fff}.description{padding:50px;padding-bottom:0;-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;justify-self:center}.description .name{font-size:22px;font-weight:700}.description .tag{color:#768696}.description .location{color:#768696}.avatar{width:100px;height:100px;overflow:hidden;border-radius:50%}.stats{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around;list-style:none;padding:0 0 30px 0}.stats .statsItem{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.stats .quantity{font-weight:700}

.statistics{margin-left:50px;margin-top:50px;background-color:white;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:flex-start;align-items:flex-start;-webkit-flex-direction:column;flex-direction:column;max-width:300px;overflow:auto;box-shadow:10px 10px 6px -6px #777}.statistics .title{margin:30px auto;color:#4F555B}.statistics .stat-list{display:-webkit-flex;display:flex;list-style:none;padding:0;margin:0}.statistics .stat-list .item{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;min-width:75px;min-height:75px;background-color:#4FC4F8;color:white}.statistics .stat-list .item:nth-child(2n){background-color:#E64C66}.statistics .label{font-size:18px}.statistics .percentage{font-size:22px}

.friendList{margin-left:50px;margin-top:50px;width:300px;list-style:none;padding:0}.friendList .online{display:block;width:20px;height:20px;overflow:hidden;border-radius:50%;background-color:#52AE50}.friendList .offline{display:block;width:20px;height:20px;overflow:hidden;border-radius:50%;background-color:#FF5152}.friendList .item{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#fff;padding:10px;box-shadow:10px 10px 6px -6px #777}.friendList .item:not(:last-child){margin-bottom:20px}.friendList .name{font-size:25px;font-weight:700}

.transaction-history{margin-left:50px;margin-top:50px;box-shadow:10px 10px 6px -6px #777;border:1px solid #c7cacf;border-radius:10px}.transaction-history .tableHeader{background-color:#00BCD5;border-radius:10px}.transaction-history .tableHeader th{color:white;border:none;padding:20px;border-radius:10px}.transaction-history .transactionBody{color:#636466}.transaction-history .transactionBody .transactionItem{text-align:center}.transaction-history .transactionBody .transactionItem:nth-child(2n){background-color:#FFFFFF}

